.logo {
	font-family: 'Shadows Into Light', cursive;
	font-size: 36px;
	color: rgba(51, 192, 201, 1);
}

a {
	color: rgba(51, 192, 201, 1);
	margin: 0 5%;
	font-weight: bold;
}

.end {
	display: flex;
	justify-content: end;
	align-items: end;
	margin: 10px;
}

.header {
	padding: 0 5% 0 1%;
	align-items: center;
	justify-content: space-between;
	display: flex;
	flex-direction: row;
	width: 100%;
	height: 8vh;
	background: rgba(51, 192, 201, 0.1);
}

.checks {
	display: flex;
	flex-direction: row;
}

.radio label {
	cursor: pointer;
	padding: 0px 15px;
	line-height: 34px;
	border: 1px solid #999;
	border-radius: 6px;
	user-select: none;
}

.header-left {
	display: flex;
	align-items: center;
	flex-direction: row;
}

.header-left > * {
	padding: 0px 10px 0px 5px;
}

.header-right {
	display: flex;
	flex-direction: column;
}

.Accordeon {
	display: flex;
	flex-direction: column;
	justify-content: start;
	align-items: start;
	background-color: rgba(51, 192, 201, 0.1);
	margin: 10px 0 0 0;
}

.button {
	background-color: rgba(51, 192, 201, 1);
	color: white;
	font-size: 20px;
	padding: 5px 5px;
	border-radius: 5px;
	margin: 20px 15px;
	cursor: pointer;
	width: 290px;
}

.button:disabled,
.button[disabled] {
	border: 1px solid #999999;
	background-color: #e6e6e6;
	color: #3e3e3e;
	cursor: not-allowed;
}

.courses {
	height: 100vh;
	width: 90vw;
}

.log {
	padding: 10px;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.texto {
	display: flex;
	justify-content: center;
	width: 100%;
	font-size: 12px;
}

.wrapper {
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
}

.Login {
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	width: 400px;
	border: 3px solid rgba(51, 192, 201, 1);
	border-radius: 5px;
	padding: 10px;
}

.TextInput {
	outline: none;
	width: 100%;
}

.justify {
	display: flex;
	justify-content: space-between;
}

.search {
	width: 20%;
	margin: 10px 0px;
}

i {
	color: rgba(51, 192, 201, 1);
	margin-right: 5px;
}

.InputRow {
	display: flex;
	width: 100%;
	flex-direction: row;
	margin: 10px;
	padding: 7px;
	border: 2px solid rgba(51, 192, 201, 1);
}

@media (max-width: 768px) {
	.header {
		padding: 0 5%;
		height: 10vh;
	}
}

@media (max-width: 768px) {
	.search {
		width: 100%;
		margin: 10px 0px;
	}
}
